.Banner-container {
  position: fixed;
  right:0px;
  bottom: 0px;
  margin-right: 70px;
  margin-bottom: 70px;
  -webkit-box-shadow: 0px 0px 10px rgba(0,0,0,0.16);
          box-shadow: 0px 0px 10px rgba(0,0,0,0.16);
  width: 700px;
  height: 120px;
  background: #fff;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  padding: 0px 20px;
  z-index: 999;
}

.Left-container {
  -ms-flex-direction: row;
      flex-direction: row;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.Logo-container {
  width: 88px;
  height: 88px;
  background: #FF952B;
  -webkit-box-shadow: 0px 0px 10px rgba(222,120,19,0.36);
          box-shadow: 0px 0px 10px rgba(222,120,19,0.36);
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
}

.Text-container {
  margin-left: 20px;
}

.Title-primary {
  font-size: 48px;
  font-weight: bolder;
  text-align: left;
}

.Title-secondary {
  font-size: 16px;
  text-align: left;
  color: #aaa
}

.Logo {
  /* width: 88px;
  height: 88px; */
  display: -ms-flexbox;
  display: flex;
  -ms-flex:1 1;
      flex:1 1;
  height: 55px;
  fill: white
}

.Download-button {
  padding: 5px 30px;
  background: #000;
  -webkit-box-shadow: 0px 0px 10px rgba(0,0,0,0.16);
          box-shadow: 0px 0px 10px rgba(0,0,0,0.16);
  color: white;
  font-weight: bold;
  font-size: 16px;
  border: none;
}

.Download-button:hover {
  background: #666666;
  cursor: pointer;
}

.Download-button:active {
  outline: none;
  outline-color: none;
}

@media screen and (max-width: 800px) {
  .Banner-container {
    position: relative;
    right:0px;
    bottom: 0px;
    margin-right: 0px;
    margin-bottom: 0px;
    -webkit-box-shadow: 0px 0px 10px rgba(0,0,0,0.16);
            box-shadow: 0px 0px 10px rgba(0,0,0,0.16);
    width: calc(100% - 40px);
    height: 120px;
    background: #fff;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    padding: 0px 20px;
    z-index: 999;
  }

  .Title-primary {
    font-size: 24px;
    font-weight: bolder;
    text-align: left;
  }
  
  .Title-secondary {
    font-size: 12px;
    text-align: left;
    color: #aaa
  }

  .Download-button {
    padding: 5px 10px;
    background: #000;
    -webkit-box-shadow: 0px 0px 10px rgba(0,0,0,0.16);
            box-shadow: 0px 0px 10px rgba(0,0,0,0.16);
    color: white;
    font-weight: bold;
    font-size: 16px;
  }
}


