.App {
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-direction: row;
      flex-direction: row;
  height: 100%;
  z-index: -1;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.App-content {
  padding: 0px 70px 0px 70px;
  -webkit-box-shadow: 0px 0px 10px rgba(0,0,0,0.16);
          box-shadow: 0px 0px 10px rgba(0,0,0,0.16);
  height: 100%;
  background: #fff;
  min-height: 1080px
}

.App-slideshow {
z-index: -1;
display: block;
width: calc(100% - 740px);
height: 100%;
position: fixed;
right: 0px;
top: 0px;

}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@media screen and (max-width: 800px) {
  .App {
    text-align: center;
    display: -ms-flexbox;
    display: flex;
    -ms-flex: 1 1;
        flex: 1 1;
    -ms-flex-direction: column-reverse;
        flex-direction: column-reverse;
    width: 100%;
    height: auto;
    z-index: -1;
  }


  .App-content {
    padding: 0px 20px 0px 20px;
    -webkit-box-shadow: 0px 0px 10px rgba(0,0,0,0.16);
            box-shadow: 0px 0px 10px rgba(0,0,0,0.16);
    height: auto;
    background: #fff;
    min-height: 1080px;
    width:calc(100%-40px)
  }

  .App-slideshow {
    z-index: -1;
    display: block;
    width: 100%;
    height: auto;
    position: relative;
    /* right: 0px; */
    /* top: 0px; */
    
    }
}



